import React from 'react'

import { makeStyles, useTheme } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';


import IconButton from '@material-ui/core/IconButton';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import Link from '@material-ui/core/Link';


import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: '100%',
        display: 'flex',
        flexDirection: 'column'

    },
    media: {
        height: '70vh',
    },
    content: {
        maxWidth: '70%',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItmes: 'center'
    },
    controls: {
        display: 'flex',
        alignItems: 'center',
        paddingLeft: theme.spacing(1),
        paddingBottom: theme.spacing(1),
    },
}));
function TechSkills() {
      const classes = useStyles();
    const theme = useTheme();
    return (
        <div className={classes.controls}>
            <a href="https://web.facebook.com/hallofinsch/">
                <IconButton aria-label="play/pause">
                    <FacebookIcon className={classes.playIcon} />
                </IconButton>
            </a>
            <a href="https://github.com/zaichone/">
                <IconButton aria-label="play/pause">
                    <GitHubIcon className={classes.playIcon} />
                </IconButton>
            </a>
            <a href="https://twitter.com/HalloFinsch">
                <IconButton aria-label="play/pause">
                    <TwitterIcon className={classes.playIcon} />
                </IconButton>
            </a>
            <a href="https://www.linkedin.com/in/zaichone/">
                <IconButton aria-label="play/pause">
                    <LinkedInIcon className={classes.playIcon} />
                </IconButton>
            </a>

        </div>
    )
}

export default TechSkills
